	module.exports = {"title":"学生時代に制作したメディアアートたち","createdAt":"2016-12-26","path":"/mediaart","body":"<ul>\n<li>かささす（2008）</li>\n<li>あしおとリズム（2009）</li>\n<li>628.（2009）</li>\n<li>結（2010）</li>\n<li>TimeMachine（2011）</li>\n</ul>\n<br />\n<br />\n<h2>かささす</h2>\n<p><img src=\"/images/mediaart/kasasasu/thumb.jpg\" alt=\"\"></p>\n<p>かささすは、当時の研究室の先輩である渡邊由香里と制作した「傘をさすという行為が奏でる音の世界」を体験するインタラクティブ・インスタレーションです。<br>\n以下、渡邊のコメントです。</p>\n<blockquote>\n<p>日常の中にあるさまざまな音が、自分の何気ない動きに伴って変化する。そんな”音という感覚”のおもしろさに気がついたとき、それまでとは違った”感覚の景”が目の前に広がります。このインスタレーションは、そんな日常の風景の中で繰り広げられる、人の何気ない行為と音という感覚の変化に焦点をあて、その行為と、それに伴う感覚を体感してもらう為のインスタレーションです。<br>\n傘をさすという行為が奏でる音の世界を、光の雨粒と傘から伝わる雨粒の音と振動で、幻影とリアリティーの融合した音の世界を表現しています。<br>\nこのインスタレーションをとおして、より豊かな感覚で日常の世界を感じてもらえればと思います。（渡邊）</p>\n</blockquote>\n<p><img src=\"/images/mediaart/kasasasu/anime01.gif\" alt=\"\"></p>\n<p><img src=\"/images/mediaart/kasasasu/anime02.gif\" alt=\"\"></p>\n<p><img src=\"/images/mediaart/kasasasu/anime03.gif\" alt=\"\"></p>\n<div class=\"Credit\">\n<h4>Credits</h4>\n<ul>\n<li>Planning: 渡邊 由香里</li>\n<li>Development: 渡邊 由香里, 井出 優太</li>\n<li>&copy; 2008 Yukari Watanabe</li>\n</ul>\n</div>\n<br />\n<br />\n<h2>あしおとリズム</h2>\n<p>この作品は渡邊の卒業制作として制作しました。</p>\n<p><img src=\"/images/mediaart/ashiotorythem/thumb.jpg\" alt=\"\"></p>\n<p>あしおとリズムは、歩く感覚から発想した音と光のインタラクティブ・インスタレーションです。<br>\n以下、渡邊のコメントです。</p>\n<blockquote>\n<p>幼い頃履いたピーピーサンダルの感覚や、落ち葉や霜柱などを踏みしめた時の感覚、砂浜や降り積もった雪の上を歩く感覚など、足音の連続性や（非）規則性に心地よさを覚え、自分の足跡を残していくことに楽しさを感じる、そんな人の感覚に触れるデバイスです。人が普段、あまり意識することのない自分の足音を、音と光で表現し、自分の通った後を、音と光が追いかけてきて軌跡として残る、そこに放たれる光と音が作るリズムを心地よく感じ、歩いたり、スキップしたりすることが、なぜか楽しくなってしまう、そんな作品です。（渡邊）</p>\n</blockquote>\n<p><img src=\"/images/mediaart/ashiotorythem/anime01.gif\" alt=\"\"></p>\n<p><img src=\"/images/mediaart/ashiotorythem/anime02.gif\" alt=\"\"></p>\n<br />\n<br />\n<div class=\"Credit\">\n<h4>Credits</h4>\n<ul>\n<li>Planning: 渡邊 由香里</li>\n<li>Development: 渡邊 由香里, 井出 優太</li>\n<li>&copy; 2009 Yukari Watanabe</li>\n</ul>\n</div>\n<h2>628.</h2>\n<p>この作品から、SENSCAPE DESIGNというユニットを組んで制作を始めています。</p>\n<p><img src=\"/images/mediaart/628/thumb.jpg\" alt=\"\"></p>\n<p>628.（ロクニハチドット）は、音と光が360度縦横無尽に動き回るのインタラクティブ・インスタレーションです。人とコンピュータの関わり方をコンセプトに、3つのパターンで抽象化し体験できるようにした作品です。</p>\n<p>人が輪の中にいる時といない時とで動きが変化します。</p>\n<p>人が輪の中に入る前は、静かな動きと優しい光が、コンピュータの機械的な冷たさや複雑な内面を隠した状態を表しています。</p>\n<p><img src=\"/images/mediaart/628/anime01.gif\" alt=\"\"></p>\n<p>人が輪の中にいる時は、人とコンピュータが関わっている状態を表しています。人が輪の中に入り介入している時の動きのパターンは3つで、それぞれ人とコンピュータが対峙した時に起こる様を表しています。</p>\n<p>1つ目のパターンは、光と音が目で追える早さで左右に方向を変えながら動いており、人とコンピュータが相互に想定内の働きをしている状態を表しています。<br>\n2つ目のパターンは、光と音の動くスピードが変化しながら動いており、コンピュータの持つ力強さや、人の能力を飛躍的に高めてくれる可能性を表現しています。<br>\n3つ目のパターンは、光と音が628カ所をランダムに移動するもので、コンピュータのもつ人智を越えた力の暴走を表してます。</p>\n<p><img src=\"/images/mediaart/628/anime02.gif\" alt=\"\"></p>\n<p><img src=\"/images/mediaart/628/anime03.gif\" alt=\"\"></p>\n<p>ユーザーフレンドリーへと進化していく機械の本質を、プリミティブな形で浮かび上がらせることを試みた作品です。機械のもつ機能や可能性、限界といった、肌で感じ取ることの難しい機械の本質的なイメージを、628個の光るドットとそれに伴って躍動する音の風景の中に込めています。</p>\n<div class=\"Credit\">\n<h4>Credits</h4>\n<ul>\n<li>Planning: 戸田 傑, 渡邊 由香里, 井出 優太</li>\n<li>Hardware Design and Development: 戸田 傑, 渡邊 由香里, 井出 優太</li>\n<li>Software Design and Development: 井出 優太</li>\n<li>&copy; 2010 SENSCAPE DESIGN</li>\n</ul>\n</div>\n<h2>結</h2>\n<p><img src=\"/images/mediaart/yui/thumb.jpg\" alt=\"\"></p>\n<p>結は、関係性（インタラクティビティ）というキーワードから発想したゲーム機です。このゲームは、2つの異なるゲームが互いのゲームを成り立たせるように作られています。</p>\n<p>傾きデバイスは、迫ってくる壁に囲われないように光を動かして、一点のゴールを目指すゲームです。デバイスを傾けると、傾けた方向に光が進みます。光は一定時間その場に止まっていると自動的に負けと判定されてしまいます。</p>\n<p>ボタンデバイスは、一点のゴールを目指して動き回る光を、壁で囲い閉じ込めるゲームです。ボタンを押すことで壁を作り、光の動きを止めることができます。壁は4つまで作ることができ、新しく作るごとに古い壁は消えていきます。</p>\n<p><img src=\"/images/mediaart/yui/anime01.gif\" alt=\"\"></p>\n<p><img src=\"/images/mediaart/yui/anime02.gif\" alt=\"\"></p>\n<p><img src=\"/images/mediaart/yui/anime03.gif\" alt=\"\"></p>\n<p><img src=\"/images/mediaart/yui/anime04.gif\" alt=\"\"></p>\n<p>他者との直接的な関わりだけでなく、目に見えない間接的な関わりが重要な結果をもたらすこともあるという「関係性」を表現した作品です。</p>\n<div class=\"Credit\">\n<h4>Credits</h4>\n<ul>\n<li>Planning: 戸田 傑, 井出 優太</li>\n<li>Hardware Design: 戸田 傑, 井出 優太</li>\n<li>Hardware Development: 井出 優太</li>\n<li>Software Design and Development: 井出 優太</li>\n<li>&copy; 2010 SENSCAPE DESIGN</li>\n</ul>\n</div>\n<h2>TIME MACHINE</h2>\n<p><img src=\"/images/mediaart/timemachine/thumb.jpg\" alt=\"\"></p>\n<p>TIME MACHINEは、同一時間軸上に複数の視点を持つことはタイムマシンを手に入れたも同然だと言えるのではないか？という考えから生まれました。</p>\n<blockquote>\n<p>人間は、同一時間軸上にひとつの視点しか持つことができない。もし、自分を外側から見る視点や、見過ごしている様々な視点を持つことができるとしたら、それはタイムマシンを手に入れたのも同然だ、と言えるだろう。<br>\n同一時間軸上に同時並行的に存在する視点を切り取り、時間の流れを与える事で見えてくる過去と未来の新たな結節点。<br>\nこの作品は、32の視点をネットワーク制御することで、普段見ることのできない視点を創りだしている。<br>\nマシンは、常に一定の間隔で世界を記録し続け、たった今過ぎ去った過去の視点を画面に刻々と映し出す。<br>\n体験者は、過去の自分が映し出されている画面を見ている間、画面の後ろに流れる風景を見過ごしてしまうが、画面に触れているほんの数秒間だけ見過ごしたはずのもうひとつの視点で、はかなく消えていく世界を捉えることができる。</p>\n</blockquote>\n<p><img src=\"/images/mediaart/timemachine/anime01.gif\" alt=\"\"></p>\n<p><img src=\"/images/mediaart/timemachine/anime02.gif\" alt=\"\"></p>\n<p><img src=\"/images/mediaart/timemachine/anime03.gif\" alt=\"\"></p>\n<p>直径2mの円に16台のiPadが設置され、隣り合うiPadとそれぞれ無線ネットワークで繋がっており、互いの動きを監視している。\niPadは一周1.6秒という早さで順番に写真を撮影し、撮影した画像を画面に表示することで、円の中にひとつの時間軸が立ち上がるようにプログラムされている。\n人がiPadの画面に触れていないときは、過去をイメージした内側のタイムスケールが反時計回りに展開し、画面に触れると未来をイメージした外側のタイムスケールが時計回りに展開する。</p>\n<div class=\"Credit\">\n<h4>Credits</h4>\n<ul>\n<li>Planning: 戸田 傑, 井出 優太</li>\n<li>Hardware Design and Development: 戸田 傑, 井出 優太</li>\n<li>Software Design and Development: 井出 優太</li>\n<li>&copy; 2011 SENSCAPE DESIGN</li>\n</ul>\n</div>\n"}

