	module.exports = {"title":"料理におけるインタラクション・デザイン - 調理行為と情報の関係性とフィジカルプロトタイプの制作","createdAt":"2017-01-04","path":"/tau-master","body":"<p>この記事では、大学院での研究内容をまとめています。</p>\n<h2>なぜこの研究テーマを選んだか</h2>\n<p>インタラクションデザインを研究することを目的に大学院へ入学した僕は、良い題材となりそうなテーマを探していました。その中で、料理には様々な道具、複雑な情報が溢れているにもかかわらず、紙やウェブ、動画でレシピを解説する以上のサポートを実際に行えてはいないと感じました。そこで、どういったインターフェイスや情報設計があれば、料理や料理に付随する活動をより楽しく便利にできるかを研究することに決めました。</p>\n<h2>論文要約</h2>\n<h3>1. 研究の目的</h3>\n<p>本研究の目的は、食に関わる情報と人との関係（インタラクション）から情報技術による支援についてのデザインを研究することである。料理におけるインタラクションデザインの基礎研究として、調理中の行為を中心に調理のためのインタラクションデザインの研究を行う。</p>\n<h3>2. 研究の方法</h3>\n<ol>\n<li>料理における行為と思考、食材と道具の扱われかたを観察し、料理を構造化する方法を探る。</li>\n<li>構造化された情報からインタラクションモデルを抽出することで、調理を支援するための行為と情報の関係性を見出す。</li>\n<li>その関係性をもとに、プロトタイプの制作を行いながら調理支援のありかたを考察する探索的な方法をとる。</li>\n<li>プロトタイプを元に、新しいインターフェース／コンテンツについて研究、制作を行う。</li>\n</ol>\n<h3>3. 観察</h3>\n<p>まず、調理の観察を行う。調理中における情報利用の実態を把握するためには、記憶の再現によって行う調理である日常化された調理から行為と情報を抜き出すことが重要である。それは、調理中に情報を提供する仕組みを考える際に、すでに存在するレシピが削ぎ落してしまった部分が重要になる可能性があるためである。<br>\n本稿では、2種類の観察を行っており、観察Aは夕食を作るすべての過程を観察し、観察Bはひとつの料理を作る過程をより詳細に観察している。</p>\n<h3>4. 調理の記述と構造化</h3>\n<p>調理における行為と、それに伴う情報利用の目的を明らかにしていくために、観察で得\nた調理のプロセスを構造化する。</p>\n<h3>5. プロトタイプ</h3>\n<p>観察、記述、構造化をふまえ、調理におけるインタラクションのプロトタイプを制作し、評価実験を行う。<br>\n図1のプロトタイプ（じゃがいもの下ごしらえ）は、調理中の自然な「行為」で提示される情報を変化させることができた場合における印象や価値を検証することを目的として制作した。<br>\n図2のプロトタイプ（カレー）は、調理中のサポート機器としてどのような情報をどのように表示するか、といった表現方法やインターフェースを研究することを目的として制作した。</p>\n<h3>6. 調理行為のヴィジュアライズ</h3>\n<p>調理をサポートするインタラクションのプロトタイプから、その中で扱われるコンテンツや道具と行為の関係を重視したインタラクションの重要性を確認した。そこで、調理中の行為と関連するコンテンツとして、調理行為そのものの動きやリズムを視覚化する「調理行為のヴィジュアライズ」についての制作を行う［図3］。</p>\n<h3>7. 考察</h3>\n<p>プロトタイプの制作から、以下のようなことが明らかになった。</p>\n<ol>\n<li>支援情報は常に表示されている必要は無い。</li>\n<li>調理情報は、利用者の熟達度に応じて表示分けできるシステムの構築が重要となる。</li>\n<li>調理行為を伝えるひとつの手段として、調理行為の視覚化が有効である。</li>\n</ol>\n<p>これらを元にして、新しいインターフェースの制作を行う。</p>\n<h3>8. まな板インターフェース</h3>\n<p>これはタッチインターフェースを持つまな板のプロトタイプである［図4, 5, 6］。知りたい情報を必要なときに必要な量だけ閲覧でき、情報をモジュールとし稼働性を持たせたことで調理中に扱いやすくなっている。<br>\nまな板型のケースにiPadを挿入し、まな板として使用しながら情報を操作できるようになっている。</p>\n<h3>9. 結論</h3>\n<p>本研究では、人の行為と情報の関係性から調理中に利用する情報インターフェースを制作した。今後は、そのインターフェースの実現可能性を固めるとともに、コンテンツについての研究も行っていきたい。</p>\n<h2>日本デザイン学会発表</h2>\n<p>2012年6月22日（金）〜24日（日）に札幌市立大学芸術の森キャンパスで開催された日本デザイン学会 第59回春季研究発表大会にて下記発表を行いました。</p>\n<ul>\n<li><a href=\"https://www.jstage.jst.go.jp/article/jssd/59/0/59_217/_article/-char/ja/\">日本デザイン学会　第59回研究発表大会 発表資料</a></li>\n</ul>\n<h2>日本デザイン学会秋季企画大会学生プロポジション発表</h2>\n<p>下記ポスター発表を行い、Reflective Awardを受賞しました。</p>\n<p><img src=\"/images/taumaster/poster01.jpg\" alt=\"\">\n<img src=\"/images/taumaster/poster02.jpg\" alt=\"\"></p>\n<h2>その他</h2>\n<ul>\n<li><a href=\"http://www.idd.tamabi.ac.jp/design/en/graduate/post-6.html\">情報デザイン修士論文要約論文集</a></li>\n</ul>\n"}

