	module.exports = {"title":"バンドルカードができるまで","createdAt":"2016-12-11","path":"/vandlecard","body":"<h2>前置き</h2>\n<p>この記事は、バンドルカードができるまでにやったことをまとめています。デザイナー・フロントエンドエンジニアとして携わっているため、バックエンドやアライアンス等をどうやっていたかについての詳細な記述はありません。<br>\nまた、記述されている多くの制作物は、チームで考え制作しており、僕個人ですべての意思決定・制作を行っているわけではありません。チームメンバーは、アライアンス・事業開発2人、エンジニア2人、デザイナー1人（僕）の5人です。</p>\n<br />\n<br />\n<br />\n<h2>CLO</h2>\n<p>2015年のはじめ。CardLinkedOffer（クレジットカード連動型優待）サービスを提供していた僕たちカンムは、このCLOというサービスをどうやって成長・収益化していくかに悩んでいました。</p>\n<p>大手カード会社と手を組みサービスを展開していく中で、自社ユーザーを持たないことの難しさと不自由さに課題を感じていました。簡単な変更を加えるだけでも提携先へ提案・連携しなくてはならず、ベンチャーらしいスピーディな仕事のやり方はできていませんでした。</p>\n<p>様々な手段を考えた末に、自社カードを作り、本当のCLOを作ろうと考え始めました。</p>\n<h2>最強のカードを作ろう</h2>\n<p>カードを発行すると決め、まずはじめに行ったのはアライアンスの準備と既存サービスの調査、そして自分たちがどんなカードを作りたいのかを考えることでした。</p>\n<p>次の資金調達へ向け既存の事業を伸ばすことが前提にあったため、全社のリソースをカード事業に割くことはできなかったが、代表の八巻と僕とで作ったたたき台を元に社内で議論を始めていきました。</p>\n<p><img src=\"/images/vandlecard/v014.png\" alt=\"\"></p>\n<p class=\"Caption\">最強のカード作ってやろうぜ</p>\n<h2>調べて描いて考える</h2>\n<p>類似するサービスや人々に使われているアプリを調査し、機能やデザインを探っていきました。</p>\n<p><img src=\"/images/vandlecard/v003.png\" alt=\"\"></p>\n<p class=\"Caption\">類似サービスを探る</p>\n<p>安心して使える、拡張性がある、データから新しい発見ができるようになるという3つの軸を元に、いくつかのアイデアだしを行いました。</p>\n<p><img src=\"/images/vandlecard/v002.png\" alt=\"\"></p>\n<p class=\"Caption\">特徴を考える</p>\n<p>集めた情報やアイデアを元に描いて考えていきました。</p>\n<p><img src=\"/images/vandlecard/v001.png\" alt=\"\"></p>\n<p class=\"Caption\">描いて考える</p>\n<p>大きく分けると、以下のようなサービスに可能性がありそうだと考えました。</p>\n<ul>\n<li>家計の管理カード</li>\n<li>子供のお小遣い</li>\n<li>グループカード</li>\n<li>経費精算カード</li>\n<li>海外旅行者カード</li>\n<li>修学旅行カード</li>\n<li>小遣い稼ぎカード</li>\n</ul>\n<h2>とにかく作って触ってみる</h2>\n<p>UIのプロトタイピングを行いながら、より具体的にサービスのイメージを膨らませていきました。\nこれらのプロトタイプは、基本的にはDropboxで共有し、スマホで見た場合の感触を元に検討していきました。UIがある程度詰まった段階でProttなどで簡単に動くものを作って確認していました。</p>\n<p><img src=\"/images/vandlecard/v006.png\" alt=\"\"></p>\n<p class=\"Caption\">UIプロトタイピング 1</p>\n<p><img src=\"/images/vandlecard/v007.png\" alt=\"\"></p>\n<p class=\"Caption\">UIプロトタイピング 2</p>\n<p><img src=\"/images/vandlecard/v005.png\" alt=\"\"></p>\n<p class=\"Caption\">UIプロトタイピング 3</p>\n<p><img src=\"/images/vandlecard/v004.png\" alt=\"\"></p>\n<p class=\"Caption\">ターゲットの簡単な分析</p>\n<p>ポイントやステータスがどうこうよりも「わかりやすくて使いやすい1枚のカードがあれば良い」という価値観を持つ人のためのカード。アプリのようにアップデートされていくカードを作れたら面白いのではないか？これが僕らの考えた初期のコンセプトとなりました。</p>\n<h2>初期プロトタイピング</h2>\n<p>2015年の7月。スマホで利用履歴が確認できて、通知もくるという体験をどう感じるかを探るために、プロトタイピングの準備を始めました。</p>\n<h3>目的</h3>\n<p>このプロトタイピングの目的は以下のようなものでした。</p>\n<ul>\n<li>いくつかの主要機能がユーザにどう受け取られるかを知る</li>\n<li>インターフェイスの課題を探る</li>\n<li>本開発時のコンセプトを決定する</li>\n<li>資金調達上のデモンストレーション</li>\n</ul>\n<h3>概要</h3>\n<p>2015年8月から11月まで、とあるプリペイドカード及びスマホアプリを20名ほどに利用してもらい、事前アンケート、カードとアプリの利用状況データ、事後アンケートを行いました。</p>\n<h3>プロトタイプ</h3>\n<p>実際にカードで決済した結果がアプリと連動するように、他社のカードのweb明細と利用通知メールのスクレイピングを行ってサーバのデータを更新する仕組みを作り、PhoneGapベースのスマホアプリに表示・通知されるよう設計しました。</p>\n<p><img src=\"/images/vandlecard/v005.png\" alt=\"\"></p>\n<p class=\"Caption\">プロトタイプのUI</p>\n<p>ざっくり書くと以下のような機能が含まれています。</p>\n<ul>\n<li>利用履歴のリアルタイム更新</li>\n<li>利用のリアルタイム通知</li>\n<li>予算の設定</li>\n<li>月・週・指定期間でのサマリー表示</li>\n<li>振込チャージ</li>\n<li>後払いチャージ</li>\n</ul>\n<h3>調査</h3>\n<p>事前アンケートはGoogleフォームを使って行いました。クレカを日常的に使っているかといった、日頃のお金の使い方やデモグラについてのアンケートです。</p>\n<p><a href=\"https://docs.google.com/forms/d/1S0vvaCR0gstohnuTmph-dawruS3aClNnL_VFjeFBcxY/viewform\">https://docs.google.com/forms/d/1S0vvaCR0gstohnuTmph-dawruS3aClNnL_VFjeFBcxY/viewform</a></p>\n<p>今回のプロトタイピングでは、始めに2000円の金額をチャージしたカードを渡して使ってもらい、その後使い続けるかはここの判断に任せるようにしました。</p>\n<p>2000円を使ってやめた人や、その後も自分でチャージして利用した人などに応じて事後インタビューを行い、その理由やアプリについての意見・感想をもらいました。</p>\n<h3>結果</h3>\n<p>調査結果として、以下のような洞察を得ています。</p>\n<ul>\n<li>都内では日常的な決済には交通系ICが利用されることがあるが、地方ではほとんど現金である。そのため、プリカでの支払いを便利だと感じる人がいた</li>\n<li>ほどんとの人が既存のカードを<em>乗り換える理由</em>を見出せなかった</li>\n<li>アプリでカード金額がすぐにわかることに魅力を感じる人がいた</li>\n<li>指定期間で利用サマリーが閲覧できると、簡易家計簿として機能するので便利だと感じる人がいた</li>\n<li>お金が絡んだサービスは深く考えずに敬遠する人がいる</li>\n</ul>\n<h2>デザインプロトタイピング2</h2>\n<p>プロトタイピングの洞察を元に、再度デザインのプロタイピングを行いました。作成したUI案は、Pixateを使って触れるようにし、ユーザーテスト及びヒアリングを行っています。</p>\n<p>前回のプロトタイプはシンプルな明細でしたが、ここで一度大きく振って、グラフィカルなサマリー表示を軸としたUI案を作成しました。</p>\n<p><img src=\"/images/vandlecard/v008.png\" alt=\"\"></p>\n<p class=\"Caption\">サマリー表示を軸としたUI案</p>\n<p>期間表示を簡単に操作できるようなUI案も作成しています。カレンダーUIを使って、日付や指定期間で利用履歴をみることができます。一週間など期間を指定し、横スワイプで移動できるようなUIも考えました。</p>\n<p><img src=\"/images/vandlecard/v009.png\" alt=\"\"></p>\n<p class=\"Caption\">期間表示を軸としたUI案</p>\n<p>この辺りから、カードをアプリ上に表示する方法を考え始めています。また、全体的なデザインのトーン・マナーについての調整も始めました。</p>\n<p><img src=\"/images/vandlecard/v010.png\" alt=\"\"></p>\n<p class=\"Caption\">シンプルな見た目とカードらしさを表現するUI案</p>\n<p>リサーチの結果、金融系アプリは概して複雑に見えるという事実がありました。差別化の目的もあり、バンドルカードでは<strong>シンプルに見える</strong>ということを重視しようと決め、調整を始めました。</p>\n<p><img src=\"/images/vandlecard/v011.png\" alt=\"\"></p>\n<p class=\"Caption\">よりシンプルだと感じさせるように調整したUI案</p>\n<h2>技術選定</h2>\n<p>デザインプロトタイピングと並行して、スマホアプリを開発するための技術選定を行いました。僕たちのチームでSwiftやJavaを書ける人がいなかったため、当時の選択肢としてPhoneGapかReactNativeか、またはSwift・Javaを学ぶか、というのがありました。技術選定及びフロントの開発は、僕と岡田（<a href=\"https://twitter.com/moqada\">@moqada</a>）で行っています。</p>\n<p><img src=\"/images/vandlecard/v033.png\" alt=\"\"></p>\n<p class=\"Caption\">コードを書きすぎた結果</p>\n<p>それまでReactjsでの開発を行っていたことと、PhoneGapでのパフォーマンスの低さを体感していたため、まずはReactNativeが本当に<strong>使えるのか</strong>を確認するためのプロトタイピングを行いました。</p>\n<blockquote>\n<p>ReactNativeはJavaScriptでiOS・Androidアプリを開発できるフレームワークです。 <a href=\"https://facebook.github.io/react-native/\">https://facebook.github.io/react-native/</a></p>\n</blockquote>\n<p>ところで、<a href=\"https://toggl.com/\">toggl</a> というサービスをご存知でしょうか？タイムトラッカーサービスですが、このTogglは使い勝手の良いAPIを提供してくれています。このAPIを使ってTogglクライアントをReactNativeで作成することで、フロントサイドだけでプロトタイピングができるのではないかと考えました。</p>\n<h3>ReactNativeの書き味</h3>\n<p>まずはReactNativeのチュートリアルをFlux・Reduxを導入した形に書き直し、書き味を確かめました。</p>\n<ul>\n<li><a href=\"https://github.com/ideyuta/rnTutorialMovies\">https://github.com/ideyuta/rnTutorialMovies</a></li>\n<li><a href=\"https://github.com/ideyuta/rnNavigatorSample\">https://github.com/ideyuta/rnNavigatorSample</a></li>\n<li><a href=\"https://github.com/ideyuta/rnScrollViewSample\">https://github.com/ideyuta/rnScrollViewSample</a></li>\n<li><a href=\"https://github.com/ideyuta/rnReduxSample\">https://github.com/ideyuta/rnReduxSample</a></li>\n</ul>\n<p>当時のReactNativeのバージョンが0.11（現時点で0.39までリリースされています）で、Androidへの対応はまだまだという感じでしたが、まずはiOSでどこまでいけるのかを確認しました。</p>\n<h3>プロトタイプ開発</h3>\n<p>作成したTogglクライアント「こしあん」です。\n<a href=\"https://github.com/tongariboyz/koshian\">https://github.com/tongariboyz/koshian</a><br>\nReactNative+Reduxという、2015年11月当時の最新スタックを導入してテストしています。</p>\n<p>このプロトタイピングによって、ReactNativeがNative言語で開発したようなパフォーマンスを発揮できることが確認できたため、ReactNativeで開発を進めることを決定しました。</p>\n<h2>本番開発</h2>\n<p>本番の開発は、2016年1月から始まりました。UIデザインを元にユーザーストーリーを洗い出し、仕様を仮定義していきした。</p>\n<p>この時点で、基幹システムや外部サービスとの連携は十分には行えておらず、全体的なサービスの仕様は全く決まっていませんでした。しかし、アプリの開発には時間がかかることが予測されていたため、アプリが必要とする単位でAPIを仮定義して開発を進めることにしました。</p>\n<p>バックエンドの仕様が確定した部分からアプリとつなぐAPIをFIXさせていきました。リリースの直前までAPIの変更がありましたが、阿吽の呼吸で開発を進められるレベルに訓練された開発チームだったことがなんとか乗り切れた理由かと思います。</p>\n<p>およそ9ヶ月の開発期間は、技術の変遷の激しいフロントエンドとしては長く、ReactNativeの内部コードも大きく変わりました。常に最新に追従しつつ開発していたため、手戻りが発生することも多くありましたが、その中で技術力が向上した感触もありました。</p>\n<p>結果的にiOS、AndroidともにワンソースでリリースできたのでReactNativeを選択したことはよかったかなと感じています。</p>\n<h3>OSSのフル活用</h3>\n<p>ReactNativeの良いところは、npmを利用して公開パッケージを簡単に利用できる点があります。コード自体はJavaScriptなので、これまでの膨大な資産が活かせるとともに、ReactNative用のComponentやLibraryの開発も活発です。</p>\n<p>例えば<a href=\"https://github.com/exponentjs\">exponentjs</a>という会社は優れたOSSを多数公開しています。</p>\n<p>OSSをプロダクションに積極的に取り入れるようになってから、OSSの内部コードを読むことが多くなりました。その中でより良い設計のパターンやデザインを発見したり、JavaScriptの基礎知識を多く学びました。</p>\n<h3>UIの最終調整・変更</h3>\n<p>開発中にUIを触り続けているので、細かい不整合などは気づいた時点で吸収しながらデザインの調整を行いました。<br>\n3人の開発チーム（フロントは1.5人）で進めていたため、<strong>コードが複雑化しないデザイン</strong>であることも重視していました。</p>\n<p><img src=\"/images/vandlecard/v012.png\" alt=\"\"></p>\n<p class=\"Caption\">実装可能性を考慮し最終調整したUI</p>\n<p>合わせて最終的なカラー定義も作成しています。</p>\n<p><img src=\"/images/vandlecard/v017.png\" alt=\"\"></p>\n<p class=\"Caption\">カラー定義</p>\n<h2>カード券面</h2>\n<p>カード券面のデザインは、本番開発とは切り離されたスケジュールで進んでいました。Visa・印刷会社・基幹システム・カンム間で多くのやりとりが行われるため、非常にスケジュールが立てづらくなっていたからです。</p>\n<p>カード券面は、当初から真っ白のカードでいこうと決まっていました。</p>\n<p><img src=\"/images/vandlecard/v015.png\" alt=\"\"></p>\n<p class=\"Caption\">カード券面</p>\n<p>なぜ白いカードにしようと思ったのか。それは最もシンプルなカードを作りたいという思いがあったからです。アップルの陶器のような白さを持つカードが出来たら、昨今のよくわからないグラフィックで覆われたカードよりも魅力的になるのではないかと考えました。</p>\n<p>実際のカードは、印刷会社の制約によって理想とはだいぶかけ離れた仕上がりとなっています。色やフォントの制約が多く存在し、カスタマイズはできません（多くのお金をかければできるとも言えます）。また、印刷品質の低さや印字の汚さなどの問題もあります。自分の力のなさを嘆きたくなりました。</p>\n<p><img src=\"/images/vandlecard/v019.png\" alt=\"\"></p>\n<p class=\"Caption\">バーチャルカード券面</p>\n<h2>シンボル</h2>\n<p>シンボルマークはかなり悩みました。もともとロゴやマークは得意分野ではなく、良いものができたとはあまり思っていません…。</p>\n<ul>\n<li>カードであることがわかる</li>\n<li>常に新しいことを取り入れていくカードである</li>\n<li>堅苦しさのないカードである</li>\n<li>どこよりも柔軟に社会の変化に対応するカードである</li>\n</ul>\n<p>以上のような考えを元に、カードのイメージをベースとしてたまごや小鳥のようなイメージを合わせてシンボルを作成しました。</p>\n<p><img src=\"/images/vandlecard/v016.png\" alt=\"\"></p>\n<p class=\"Caption\">シンボルマーク</p>\n<h2>ネーミング</h2>\n<p>バンドルカードは、ValueとHandleから作った造語です。よりよくお金を使えるようにするというビジョンの元、このカードは価値を扱いやすくしてくれるものであるという考えからつけた名前です。</p>\n<p>シンボルマークとも、「鳥」をキーワードに印象がぶれないようにと考えましたが、そううまくいっているかはちょっとわかりません…。</p>\n<h2>パッケージ</h2>\n<p>プラスチックカードを送付するためのパッケージを作成しました。\nカード、説明書、カード台紙、封筒、宛名シールなど、をいかに<strong>単価を最小にして簡単に封入できる形にするか</strong>を検討しました。</p>\n<p>送付のオペレーション上、カード裏面にあるIDをユーザーと結びつける必要があり、外部に委託するには精密なアセンブリ設計をする必要があることがわかりました。また、アセンブリ工程の複雑さが費用の増加に直結するため、いかに単純化、分担化できるかが問題となりました。</p>\n<p>まずは、台紙をなくし、ビニールの袋にすべてを包むようにすることでコスト減と作業の簡略化を実現しています。（特殊な台紙の印刷よりもビニールの袋の方が安価、かつアセンブリ作業が簡単）</p>\n<p><img src=\"/images/vandlecard/v018.png\" alt=\"\"></p>\n<p class=\"Caption\">パッケージ</p>\n<p>ほとんどの他社カードが定形封筒の味気ない形で送られてくる中で、僕らはそれを変えるべきだと考えていました。カード会社が物理的な接点を持つ機会として、リアルカードの送付という接点は非常に重要です。</p>\n<p>ユーザーに驚きを与えられるような、そんな送付パッケージにしたいという思いがありました。革のカードケースに入って送られてきたら嬉しいんじゃないかとか、そういったことを考えながら試作や業者との連携を進めていましたが、現実的に難しいという結果になりました。</p>\n<p>物理的な送付物を用意しオペレーションを整備するのは想像以上に難易度が高く、費用という意味でも妥協せざる負えない部分が多々ありました。</p>\n<h2>ランディングページ</h2>\n<p>2016年7月11日、ランディングページを公開しました。これまで作成してきたリソースを使い、2週間程でデザインから開発まで行っています。<a href=\"https://github.com/gatsbyjs/gatsby\">gatsbyjs</a>というReactベースのサイトジェネレータを使って作成しています。</p>\n<p>このライブラリの良いところはJavaScriptで記述できることで、webpackが組み込まれておりReact、PostCSSといったアセットを標準で持っていることです。さらにサーバサイドレンダリングでHTMLファイルをパブリッシュできるので、SEOの観点から見ても問題がないというのもあります。</p>\n<p>また、Markdownでも簡単に記事ページを作成することができる点も大きいです。</p>\n<p><img src=\"/images/vandlecard/v020.png\" alt=\"\"></p>\n<p class=\"Caption\">リリース前LP</p>\n<h2>カードが来たぞ</h2>\n<p>2016年の9月。印刷会社から本番カードが送られてきました。リリースの数日前のことです。</p>\n<p><img src=\"/images/vandlecard/v027.png\" alt=\"\"></p>\n<p class=\"Caption\">リアルカードを初めてアクティベーションした男</p>\n<h2>リリース</h2>\n<p>2016年9月16日、カンム、オリコさん、TISさん、昌栄印刷さんで作りあげたバンドルカードを無事リリースできました。</p>\n<p><img src=\"/images/vandlecard/v028.png\" alt=\"\"></p>\n<p class=\"Caption\">App Store</p>\n<h2>高校生プロモーション開始</h2>\n<p>バンドルカードのプロモーションとして、まずは高校生をターゲットにしたプロモーションを開始しました。</p>\n<p><img src=\"/images/vandlecard/v029.png\" alt=\"\"></p>\n<p class=\"Caption\">無限ポテト</p>\n<p>4人チームを作ってポテトを増やしまくり、スコアの一番高かった人が賞金（250円）を受け取れるというプロモーションをクローズで開始しました。しかし、ゲームを始めるにはバンドルカードのアカウントが必要かつ4人集めなければならないというのが想像以上のハードルとなり、ほとんど普及せずに終了しました。</p>\n<p><a href=\"https://vandle.jp/lp-campaign-potato/\">https://vandle.jp/lp-campaign-potato/</a></p>\n<p>50人ほどの高校生に初期ユーザとして参加してもらい、SNSを通じて拡散を行いましたがほとんど利用されませんでした。しかし、カード自体を便利だと感じる高校生が半数ほどいたり、ゲーム自体の面白さは実証でき、始めるまでのハードルが問題であることがわかりました。</p>\n<p><img src=\"/images/vandlecard/v030.png\" alt=\"\"></p>\n<p class=\"Caption\">タップすると増え続ける...</p>\n<h2>ハロウィンイベント</h2>\n<p>バンドルカードプロモーションとして、ingさんに協力してもらい高校生限定ハロウィンイベントに協賛しカード配布を行いました。</p>\n<p><img src=\"/images/vandlecard/v021.jpg\" alt=\"\"></p>\n<p class=\"Caption\">ハロウィンイベント</p>\n<p>このイベントの準備は本当に時間がなく、全社で印刷物まで手で作るということをやりました…</p>\n<p><img src=\"/images/vandlecard/v026.png\" alt=\"\"></p>\n<p class=\"Caption\">全員でイベント準備</p>\n<h2>簡単ガイドブック</h2>\n<p>イベント等での配布用に二つ折りのガイドブックを作成しました。</p>\n<p><img src=\"/images/vandlecard/v022.jpg\" alt=\"\"></p>\n<p class=\"Caption\">ガイドブック表紙</p>\n<h2>竹下通りサンプリング</h2>\n<p>マイナビさんに協力してもらい、竹下通りのJOL原宿前でチラシの配布と、カードの登録ブースを設置してカード配布を行いました。</p>\n<p><img src=\"/images/vandlecard/v023.png\" alt=\"\"></p>\n<p class=\"Caption\">竹下通りでチラシを配布</p>\n<p><img src=\"/images/vandlecard/v024.png\" alt=\"\"></p>\n<p class=\"Caption\">バンドルカードブース</p>\n<p><img src=\"/images/vandlecard/v025.jpg\" alt=\"\"></p>\n<p class=\"Caption\">配布物</p>\n<h2>Androidリリース</h2>\n<p>2016年12月16日。Android版をリリース。</p>\n<p>同時にリリース記念キャンペーンを開催</p>\n<p><img src=\"/images/vandlecard/v031.png\" alt=\"\"></p>\n<p class=\"Caption\">Androidリリース記念キャンペーン</p>\n<h2>お年玉キャンペーン</h2>\n<p>12月から入ってくれている中川君の発案で2017年に向けてお年玉キャンペーンを実施。これまでにないフォロワー・会員獲得率を記録している。</p>\n<p><img src=\"/images/vandlecard/v032.png\" alt=\"\"></p>\n<p class=\"Caption\">お年玉キャンペーン</p>\n<h2>無限ポテトキャンペーン改</h2>\n<p>前回の無限ポテトのリベンジとして、ゲーム参加までのハードルを下げたり、画像の投稿などの機能をつけたWebアプリを作成。</p>\n<p><a href=\"https://vandle.jp/p/otato/#/\">https://vandle.jp/p/otato/#/</a></p>\n<blockquote class=\"twitter-tweet\" data-lang=\"ja\"><p lang=\"ja\" dir=\"ltr\">のんがいっぱい。じわ。<br>得点は、35,817点でした。<br><br>30,000点を超えると<br>300円GET出来るらしいので、<br>皆さんチャレンジしてみてください。<a href=\"https://twitter.com/hashtag/%E3%83%90%E3%83%B3%E3%83%89%E3%83%AB?src=hash\">#バンドル</a> <a href=\"https://twitter.com/hashtag/300%E5%86%86%E3%83%97%E3%83%AC%E3%82%BC%E3%83%B3%E3%83%88?src=hash\">#300円プレゼント</a> <a href=\"https://twitter.com/hashtag/PR?src=hash\">#PR</a> <a href=\"https://t.co/3SxXYJKQb8\">https://t.co/3SxXYJKQb8</a> <a href=\"https://t.co/zAY6Noxhqq\">pic.twitter.com/zAY6Noxhqq</a></p>&mdash; 前田希美 (@Maeda_Nozomi) <a href=\"https://twitter.com/Maeda_Nozomi/status/814737564749864960\">2016年12月30日</a></blockquote>\n<blockquote class=\"twitter-tweet\" data-lang=\"ja\"><p lang=\"ja\" dir=\"ltr\">POTATOかっぷるの画像を使ったゲーム、１回やってみて（笑）<br>指疲れる😂<br>連打しまくるゲームです（笑）<br><br>特典付きです🍟✨<br>     <br>   ちなみにおれは34089点！<br><br>みんな越えてみろ！！！！！（笑）<br>#バンドル#ポテト<a href=\"https://t.co/QCaWnud729\">https://t.co/QCaWnud729</a> <a href=\"https://t.co/p1P4Oiwst7\">pic.twitter.com/p1P4Oiwst7</a></p>&mdash; みーくん⍢⃝ (@0727_Miz_Kam) <a href=\"https://twitter.com/0727_Miz_Kam/status/814741189752565760\">2016年12月30日</a></blockquote>\n<h2>複数カード対応</h2>\n<p>いくつかのプロモーションを繰り返す中で、外して外して時には当たったりしていましたが、より着実にカード保有者を増やす方法として、「バンドルカード」以外の提携カードを発行することになりました。</p>\n<p>そこで、複数のカードに対応したアプリの開発が始まりました。</p>\n<p>どのカードの明細を見ているか、どのカードにチャージを行おうとしているか、といったことを直感的にわかりやすくするために、メイン画面ではカード情報が常に表示されるように UI を変更しました。</p>\n<p><img src=\"/images/vandlecard/v034.png\" alt=\"\"></p>\n<p class=\"Caption\">複数カード対応 UI</p>\n<p>この機能のデザインを終え、View 側の実装をほどほどに、僕のカンムとしての旅は終わりました。</p>\n<h2>バンドルカード</h2>\n<p>バンドルカードは、わかりやすく使いやすいカードがあるべきだと考えた奴らが作り、誰もがインターネットで決済できる世界に受け入れられ広まっています。</p>\n<p>愚直に続けることがカンムの文化であり、最高のプロダクトを作るために全力を傾けているチームです。</p>\n<p>ぜひこの記事を見てご興味が湧きましたらカンムまでご連絡ください。</p>\n<p><a href=\"https://kanmu.co.jp\">https://kanmu.co.jp</a></p>\n"}

